import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  Button,
  Switch,
  FormControlLabel,
  Alert,
  Box,
  Chip,
} from '@mui/material';
import {
  Notifications as NotificationsIcon,
} from '@mui/icons-material';
import { useNotifications } from '../hooks/useNotifications';
import type { VocabularyWord } from '../types';

interface NotificationSettingsProps {
  words: VocabularyWord[];
}

const NotificationSettings: React.FC<NotificationSettingsProps> = ({ words }) => {
  const {
    isSupported,
    permission,
    requestPermission,
    scheduleNotification,
    isServiceWorkerRegistered,
  } = useNotifications();

  const handleEnableNotifications = async () => {
    const result = await requestPermission();
    if (result === 'granted' && words.length > 0) {
      await scheduleNotification(words);
    }
  };

  const handleToggleNotifications = async (enabled: boolean) => {
    if (enabled) {
      await handleEnableNotifications();
    }
  };

  const handleTestNotification = () => {
    console.log('Test notification button clicked');
    console.log('Current state:', { permission, wordsLength: words.length, isSupported, isServiceWorkerRegistered });
    
    // 
    console.log('Browser notification permission:', Notification.permission);
    console.log('Notification constructor available:', typeof Notification !== 'undefined');
    console.log('User agent:', navigator.userAgent);
    
    scheduleNotification(words);
  };

  const handleSystemDiagnostics = () => {
    const diagnostics = {
      notificationPermission: Notification.permission,
      notificationSupported: 'Notification' in window,
      serviceWorkerSupported: 'serviceWorker' in navigator,
      userAgent: navigator.userAgent,
      currentUrl: window.location.href,
      timestamp: new Date().toISOString()
    };
    
    console.log('System Diagnostics:', diagnostics);
    
    // 
    try {
      const testNotification = new Notification('', {
        body: '',
        icon: '/logo192.png'
      });
      
      testNotification.onshow = () => {
        console.log('');
      };
      
      testNotification.onerror = (error) => {
        console.error(':', error);
      };
      
    } catch (error) {
      console.error(':', error);
      alert(`: ${error}`);
    }
  };

  const handleSimpleTest = () => {
    if (words.length === 0) {
      alert('');
      return;
    }

    const randomWord = words[Math.floor(Math.random() * words.length)];
    console.log('Simple test notification for word:', randomWord);
    
    try {
      const notification = new Notification('', {
        body: `: ${randomWord.word}\n: ${randomWord.meaning}`,
        icon: '/logo192.png'
      });
      
      notification.onshow = () => {
        console.log(' ');
      };
      
      notification.onerror = (error) => {
        console.error(':', error);
      };
    } catch (error) {
      console.error(':', error);
    }
  };

  const getPermissionStatus = () => {
    switch (permission) {
      case 'granted':
        return { color: 'success' as const, text: '' };
      case 'denied':
        return { color: 'error' as const, text: '' };
      default:
        return { color: 'warning' as const, text: '' };
    }
  };

  if (!isSupported) {
    return (
      <Card>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            
          </Typography>
          <Alert severity="warning">
            
          </Alert>
        </CardContent>
      </Card>
    );
  }

  const permissionStatus = getPermissionStatus();

  return (
    <Card>
      <CardContent>
        <Typography variant="h6" gutterBottom>
          <NotificationsIcon sx={{ verticalAlign: 'middle', mr: 1 }} />
          
        </Typography>
        
        <Typography variant="body2" color="text.secondary" gutterBottom>
          101
        </Typography>

        <Box sx={{ my: 2 }}>
          <Typography variant="body2" gutterBottom>
            :
          </Typography>
          <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
            <Chip 
              label={`: ${permissionStatus.text}`}
              color={permissionStatus.color}
              size="small"
            />
            <Chip 
              label={`Service Worker: ${isServiceWorkerRegistered ? '' : ''}`}
              color={isServiceWorkerRegistered ? 'success' : 'warning'}
              size="small"
            />
            <Chip 
              label={`: ${words.length}`}
              color={words.length > 0 ? 'info' : 'default'}
              size="small"
            />
          </Box>
        </Box>

        {permission === 'denied' ? (
          <Alert severity="error" sx={{ mb: 2 }}>
            
          </Alert>
        ) : null}

        {words.length === 0 ? (
          <Alert severity="warning" sx={{ mb: 2 }}>
            
          </Alert>
        ) : null}

        <FormControlLabel
          control={
            <Switch
              checked={permission === 'granted'}
              onChange={(e) => handleToggleNotifications(e.target.checked)}
              disabled={words.length === 0}
            />
          }
          label=""
        />

        {permission !== 'granted' && words.length > 0 && (
          <Box sx={{ mt: 2 }}>
            <Button
              variant="contained"
              onClick={handleEnableNotifications}
              startIcon={<NotificationsIcon />}
            >
              
            </Button>
          </Box>
        )}

        {permission === 'granted' && words.length > 0 && (
          <>
            <Alert severity="success" sx={{ mt: 2 }}>
              10
            </Alert>
            <Alert severity="info" sx={{ mt: 1 }}>
              <strong>macOS</strong>    {navigator.userAgent.includes('Chrome') ? 'Google Chrome' : ''} <br />
              <strong></strong>
            </Alert>
            <Box sx={{ mt: 2, display: 'flex', gap: 1, flexWrap: 'wrap' }}>
              <Button
                variant="outlined"
                onClick={handleTestNotification}
                startIcon={<NotificationsIcon />}
                size="small"
              >
                
              </Button>
              <Button
                variant="contained"
                onClick={handleSimpleTest}
                size="small"
                color="primary"
              >
                
              </Button>
              <Button
                variant="text"
                onClick={handleSystemDiagnostics}
                size="small"
                color="secondary"
              >
                
              </Button>
            </Box>
          </>
        )}
      </CardContent>
    </Card>
  );
};

export default NotificationSettings;